const WRITE_MESSAGE = "WRITE_MESSAGE";

let initialState = {
  name: "Mher",
  lastName: "Simonyan",
  message: null
}

const mainPageReducer = (state = initialState, action) => {
  switch(action.type) {
    case WRITE_MESSAGE:
      return {
        ...state,
        message: action.message
      }
    default:
      return state;
  }
}

export const writeMessageActionCreator = (message) => ({ type: WRITE_MESSAGE, message });

export default mainPageReducer;