import React from 'react';
import { connect } from 'react-redux';
import {
  setHTMLActionCreator,
  setCSSActionCreator,
  setJSActionCreator
} from '../Redux/Reducers/skillsReducer'

const Skills = (props) => {
  const onChange = (e, lang) => {
    switch (lang) {
      case "html":
        if (+e.target.value <= 100)
          props.setHTML(e.target.value)
        break;
      case "css":
        if (+e.target.value <= 100)
          props.setCSS(e.target.value)
        break;
      case "js":
        if (+e.target.value <= 100)
          props.setJS(e.target.value)
        break;
      default:
        return
    }
  }

  return (
    <>
      <div className="title">
        <h2>Specify your skills</h2>
      </div>
      <div className="skills">
        <div className="skillRatings">
          <p className="description">HTML Level</p>
          <div></div>
          <div style={{ width: props.htmlLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.htmlLevel} onChange={(e) => onChange(e, "html")} className="skillInput" />
        </div>
        <div className="skillRatings">
          <p className="description">CSS Level</p>
          <div></div>
          <div style={{ width: props.cssLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.cssLevel} onChange={(e) => onChange(e, "css")} className="skillInput" />
        </div>
        <div className="skillRatings">
          <p className="description">JS Level</p>
          <div></div>
          <div style={{ width: props.jsLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.jsLevel} onChange={(e) => onChange(e, "js")} className="skillInput" />
        </div>
      </div>
    </>
  )
}

const SkillsContainer = (props) => {
  return (
    <Skills {...props} />
  )
}

let mapStateToProps = (state) => ({
  htmlLevel: state.skillsPage.htmlLevel,
  cssLevel: state.skillsPage.cssLevel,
  jsLevel: state.skillsPage.jsLevel
})

let mapDispatchToProps = (dispatch) => ({
  setHTML: (htmlLevel) => {
    dispatch(setHTMLActionCreator(htmlLevel));
  },
  setCSS: (cssLevel) => {
    dispatch(setCSSActionCreator(cssLevel));
  },
  setJS: (jsLevel) => {
    dispatch(setJSActionCreator(jsLevel));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(SkillsContainer);