import React from 'react';
import { connect } from 'react-redux';
import { writeMessageActionCreator } from '../Redux/Reducers/mainPageReducer'

const MainPage = (props) => {
  const onChange = (e) => {
    props.writeMessage(e.target.value);
  }

  return (
    <>
      <h1>{props.message}</h1>
      <input type="text" value={props.message || ""} onChange={onChange} />
    </>
  )
}

const MainPageContainer = (props) => {
  return (
    <MainPage message={props.message} writeMessage={props.writeMessage} />
  )
}

let mapStateToProps = (state) => ({
  message: state.mainPage.message
})

let mapDispatchToProps = (dispatch) => ({
  writeMessage: (message) => {
    dispatch(writeMessageActionCreator(message));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(MainPageContainer);